'use strict';

/**
* Compute the maximum value of a one-dimensional double-precision floating-point ndarray.
*
* @module @stdlib/stats-base-ndarray-dmax
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import ndarray from '@stdlib/ndarray-base-ctor';
* import dmax from '@stdlib/stats-base-ndarray-dmax';
*
* var xbuf = new Float64Array( [ 1.0, 3.0, 4.0, 2.0 ] );
* var x = new ndarray( 'float64', xbuf, [ 4 ], [ 1 ], 0, 'row-major' );
*
* var v = dmax( [ x ] );
* // returns 4.0
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils-try-require';
import isError from '@stdlib/assert-is-error';
import main from './main.js';


// MAIN //

var dmax;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	dmax = main;
} else {
	dmax = tmp;
}


// EXPORTS //

export default dmax;
