'use strict';

/**
* Compute the minimum value of a one-dimensional single-precision floating-point ndarray.
*
* @module @stdlib/stats-base-ndarray-smin
*
* @example
* import Float32Array from '@stdlib/array-float32';
* import ndarray from '@stdlib/ndarray-base-ctor';
* import smin from '@stdlib/stats-base-ndarray-smin';
*
* var xbuf = new Float32Array( [ 1.0, 3.0, 4.0, 2.0 ] );
* var x = new ndarray( 'float32', xbuf, [ 4 ], [ 1 ], 0, 'row-major' );
*
* var v = smin( [ x ] );
* // returns 1.0
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils-try-require';
import isError from '@stdlib/assert-is-error';
import main from './main.js';


// MAIN //

var smin;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	smin = main;
} else {
	smin = tmp;
}


// EXPORTS //

export default smin;
